@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');
 
body {
  padding: 0px;
  margin: 0px;
  width: auto;
  font-family: 'Raleway', sans-serif;
}

#root {
  padding: 0px;
}

.page {
  margin: 0px;
  display: flex;  
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

#quote-box {
  box-shadow: 0 0 20px -4px black;
  background-color: white;
  padding: 20px;
  max-width: 500px;
  min-width: 200px;
  max-height: 400px;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#author {
  align-self: flex-end;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

#new-quote {
  color: white;
}